import './styles.css'

export default function ColorPicker(props) {
    return (
        <div
            className='colorpick-wrapper'
        >
            <div style={{ display: props.display }} className='title'>CHOOSE YOUR BACKGROUND:</div>
            <div style={{ display: props.display }} className='color-wrapper'>
                {props.colors.map((i, index) =>
                    <div
                        key={index}
                        className='color'
                        style={{
                            background: i,
                        }}
                        onClick={() => props.setColor(i)}
                    >
                        <div
                            style={{
                                background: i,
                                display: props.selectedColor === i ? '' : 'none'
                            }}
                            className='color-under'
                        />
                    </div>
                )}
            </div>
        </div>
    )
}