import { useEffect, useState, useRef } from 'react';
import algosdk from 'algosdk';
import { toPng } from 'html-to-image';
import './Banner.css';
import HeaderSwitch from './components/HeaderSwitch/HeaderSwitch';
import ColorPicker from './components/ColorPicker/ColorPicker';
import CustomText from './components/CustomText/CustomText';
import CatPicker from './components/CatPicker/CatPicker';
import Drag from './Drag';
// import logo from '../src/assets/logo192.png';
import { Puff } from 'react-loader-spinner';

const algodClient = new algosdk.Algodv2('', 'https://mainnet-api.algonode.cloud', '');

function Banner() {

  const [wallets, setWallets] = useState();
  const [selectedWallet, setSelectedWallet] = useState();
  const colors = ['#c62f41', '#bcdaff', '#a5f9dd', '#ceb9f9', '#ffbceb', '#ffc18f', '#ffed94', '#ffffff',];
  const [headerColor, setHeaderColor] = useState('#000');
  const carousel = ['Background', 'Header', 'Custom Text', 'Your CGFs'];
  const [carouselIndex, setCarouselIndex] = useState(0);
  const [mobile, setMobile] = useState()
  const [selectedColor, setSelectedColor] = useState('#fff');
  const [customText, setCustomText] = useState();
  const [isHeader, setHeader] = useState(true);
  const [isResizeLarge, setResizeLarge] = useState(true);
  const [catIds, setCatIds] = useState();
  const [catNames, setCatNames] = useState([]);
  const [catUrls, setCatUrls] = useState([]);
  const [typedWallet, setTypedWallet] = useState('');
  const [isLoading, setLoading] = useState(false);


  const ref = useRef();

  useEffect(() => {

    window.addEventListener('resize', function () {
      if (window.innerWidth < 700) {
        setMobile(true);
      } else {
        setMobile(false)
      }
    });
    return () => {
      window.removeEventListener('resize', function () {
        if (window.innerWidth < 700) {
          setMobile(true);
        } else {
          setMobile(false)
        }
      });
    }



  }, []);


  const searchWallet = async (wallet) => {

    setSelectedWallet(typedWallet);
    setLoading(true);

    let accountInfo = await algodClient.accountInformation(wallet).do();

    const IDS = accountInfo.assets.reduce(function (filtered, option) {
      if (option.amount !== 0) {
        filtered.push(option["asset-id"])
      }
      return filtered;
    }, []);

    setCatIds(IDS);

    if (IDS.length > 0) {
      let arr = []
      for (let i = 0; i < IDS.length; i++) {

        if (IDS[i] > 709294689 && IDS[i] < 715136810) {
          algodClient.getAssetByID(IDS[i]).do().then((res => {
            if (
              res.params.creator === 'SMCMOJRSEEX5PQRB4G3ACIMQ2S62LUN5XXXQIAMDOLEMYJVYOAED2D5FHU') {
              setSelectedColor('#fff')
              arr.push(res.params.name);
            } else {
            }
          }))
        }

      }

      setCatNames(arr);
      setLoading(false)
    }

  }

  const saveImage = () => {


    toPng(ref.current)
      .then(() => {
        toPng(ref.current)
          .then(() => {
            toPng(ref.current)
              .then((dataUrl) => {
                const link = document.createElement('a')
                link.download = 'SMCbanner.png'
                link.href = dataUrl
                link.click()
                setResizeLarge(true)
              })
              .catch((err) => {
                console.log(err)
              })
          })
          .catch((err) => {
            console.log(err)
          })
      })
      .catch((err) => {
        console.log(err)
      })

  }


  return (
    <>
      {!isResizeLarge ?
        <div className='blocker'>
          {/*<img src={logo} height='192' width='192' alt="Loading" />*/}
          <div>Saving...</div>
        </div>
        :
        null
      }
      <div className="app-container">
        <div className='header'>
          <div className='heading-title'>BANNER BUILDER</div>
          <div className='heading-text'>
            Showcasing your favourite SMC NFTS has never been easier. Choose your favourite kittens,background,custom text and use it as your signature in the NFT TWITTER WORLD
          </div>
        </div>

        <div
          ref={ref}
          className='image-container'
          style={{
            background: selectedColor,
            position: isResizeLarge ? 'relative' : 'absolute',
            left: isResizeLarge ? '' : '0',
            width: isResizeLarge ? '' : `${mobile ? '750px' : '1500px'} `,
            height: isResizeLarge ? `${mobile ? '33vw' : 'auto'}` : `${mobile ? '250px' : '500px'} `
          }}
        >

          <Drag
            caturls={catUrls}
            setCatUrls={setCatUrls}
            color={selectedColor}
          />

          {catNames.length === 0 && selectedWallet ?
            <div className='disclaimer'>Looks like you don't have an SMC</div>
            :

            <div className='catlogo-text' style={{
              visibility: isHeader ? 'visible' : 'hidden',
              fontSize: isResizeLarge ? `${mobile ? '9vw' : '4.5vw'}` : `${mobile ? '65px' : '130px'}`,
              color: headerColor
            }}
            >
              {customText}
            </div>

          }

          {!selectedWallet &&
            <div className='connect-container'>
              <input
                type='text'
                className='wallet-input'
                placeholder='ENTER YOUR WALLET ADDRESS'
                style={{ textTransform: "uppercase" }}
                value={typedWallet}
                onChange={(e) => setTypedWallet(e.target.value)}
                onKeyPress={event => {
                  if (event.key === 'Enter') {
                    console.log('enter')
                    searchWallet(typedWallet)
                  }
                }}
              />
              <button className='btn-main' onClick={() => searchWallet(typedWallet)}>
                CONNECT
              </button>
            </div>
          }
          {
            isLoading ?
              <div className='loading-container'>
                <Puff color="#000" />
              </div>
              :
              null
          }

        </div>

        < div className='edit-container'>

          <CatPicker names={catNames} caturls={catUrls} setCatUrls={setCatUrls} />
          <ColorPicker colors={colors} setColor={setSelectedColor} selectedColor={selectedColor} />
          <CustomText setCustomText={setCustomText} />
          <div className="bottom-row">
            <HeaderSwitch header={isHeader} setHeaderColor={setHeaderColor} />

            <button className='btn-main btn-save' disabled={catNames.length > 0 ? false : true} onClick={() => {
              setResizeLarge(false)
              setTimeout(saveImage, 100)
            }}>SAVE</button>
          </div>

        </div>

      </div>
    </>
  );
}

export default Banner;
