import { useEffect, useState } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";


// a little function to help us with reordering the result
const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};


export default function Drag(props) {

    const [testarr, setTestArr] = useState(
        props.caturls

    );

    useEffect(() => {
        setTestArr(props.caturls)
    }, [props.caturls])


    const getListStyle = (isDraggingOver, itemsLength) => ({
        background: isDraggingOver ? props.color : "transparent",
        // border: '1px solid blue',
        // position: 'absolute',
        display: "flex",
    });

    const onDragEnd = (result) => {
        // dropped outside the list
        if (!result.destination) {
            return;
        }
        const items = reorder(
            testarr,
            result.source.index,
            result.destination.index
        );
        setTestArr(items)
    }

    return (
        <div
            style={{
                // border: '1px solid green',
                position: 'absolute',
                width: `100%`,
                bottom: '0%',
                display: 'flex',
                justifyContent: 'center'
            }}
        >
            <div
                style={{
                    // border: '1px solid green',
                    position: 'absolute',
                    width: `calc(20% *${testarr.length} `,
                    bottom: '0%',
                }}
            >
                <DragDropContext onDragEnd={onDragEnd}>
                    <Droppable droppableId="droppable" direction="horizontal">
                        {(provided, snapshot) => (
                            <div
                                ref={provided.innerRef}
                                style={getListStyle(snapshot.isDraggingOver, testarr.length)}
                                {...provided.droppableProps}
                            >
                                {testarr.map((item, index) => {

                                    return (
                                        <Draggable key={`dragId${index}`} draggableId={`dragId${index}`} index={index}>
                                            {(provided, snapshot) => (
                                                <div
                                                    ref={provided.innerRef}
                                                    {...provided.draggableProps}
                                                    {...provided.dragHandleProps}
                                                >
                                                    <img
                                                        style={{
                                                            width: '127%',
                                                            height: '100%',
                                                            marginLeft: '0%'
                                                        }}
                                                        alt=""
                                                        src={`https://raw.githubusercontent.com/saymeowclub/smc/main/nobg10k/${item}.png`}
                                                    />
                                                </div>
                                            )}
                                        </Draggable>
                                    )
                                }

                                )}
                                {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </DragDropContext>
            </div >
        </div >
    );
}

