
export default function CheckBox(props) {


    const getKitty = (number) => {

        if (!props.checked) {
            if (props.caturls.length < 5) {
                props.setSelectedGeese(props.selectedGeese.concat(props.i));
                props.setCatUrls(props.caturls.concat(number));
            } else {
                alert('max cat limit reached');
            }
        } else {
            props.setSelectedGeese(props.selectedGeese.filter(function (cat) { return cat !== props.i }));
            props.setCatUrls(props.caturls.filter(function (cat) { return cat !== number }));
        }
    }

    return (
        <div className='cat' >

            <div style={{
                color: props.caturls.includes(props.i.replace('SMC #', '')) ? "#52df58" : '#000'
            }}
                onClick={() => getKitty(props.i.replace('SMC #', ''))}
            >
                {props.i}
            </div>
            {/* spacers */}
            <div></div>
            <div></div>
        </div>
    )
}
